body {
  height: 100%;
  background: linear-gradient(135deg, #044f48, #345093);
  background:#fff;
  margin: 0;
  padding: 0;
  font-family: 'Avenir', 'Open Sans', sans-serif;
  font-size: 14px;
  line-height: 1.3;
  overflow: hidden;
}

.organaizer {
  width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.chat-window {
  border: 2px solid black;
  width: 100%;
  /* height: calc(100% + 15px); */
  height: 200px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  max-height: 550px;
  overflow-y: auto;
  border-radius: 20px;
  
  /* display: flex;

  flex-direction: column;
  justify-content: flex-end; */
  
}

.chat-input {
  text-align: center;
  margin-top: 10px;
}

input {
  width: 100%;
  margin-bottom: 10px;
}

.message-container {
  width: 80%;
  min-height: 20px;
  border: 1px solid black;
  margin-top: 10px;
  margin-left: auto;
  margin-right: 10px;
  margin-bottom: 10px;

  display: flex;
}

.message {
  width: 100%;
  margin-left: 20px;
}

.buttons {
  display: flex;
  justify-content: space-between;
}

.attach-container {
  width: 100px;
  height: 40px;
  position: relative;

  cursor: pointer;
}

.attach-input {
  position: absolute;
  top:0; left: 0; bottom: 0; right: 0; margin: 0;
  opacity: 0.1;
  width: 100%;

}

.attach-icon {
  position: absolute;
  top:0; left: 0; bottom: 0; right: 0; margin: 0;
  width: 100%;
  background-color: gray;
  border-radius: 5px;
  line-height: 40px;
  text-align: center;
}

