  position: absolute;
  left: 101px;
  height: 100%;
  margin-left: 3px;
} */

.message-img {
  width: 50px;
  height: 50px;
}

.preview {
  width: 200px;
  /* height: 100px; */
  padding: 20px; 
  display: block;
  overflow: hidden;
}

img, video, audio {
  width: 100%
}